#contact {
  @include flex-center(row);
}
.contact-wrapper {
  @extend .certificates-wrapper;
}
.contact-text {
  @extend .certificates-text;
}
.contact-container {
  @extend .certificates-container;
}
.contact-card-img {
  height: 200px !important;
  width: 200px !important;
  padding: 0.5rem 1rem !important;
}
@include media-sm {
  .contact-text {
    font-size: 1.5rem;
  }
}
